<template>
    <div class="real-time-video-page">

      <div class="secondary-page-mode">
         <div class="video-content-wrap">
           <ul class="video-content-list" :class="videoNumber">
              <li :key="index" v-for="item,index in videoData">
                <div class="video-box">
                  <!-- <object pluginspage="http://www.macromedia.com/go/getflashplayer"
                  <embed allowscriptaccess="always" allowfullscreen="true" quality="high" bgcolor="#FFF" wmode="transparent" src="https://open.ys7.com/sdk/js/1.3/ckplayer/ckplayer.swf" flashvars="f=rtmp://rtmp.open.ys7.com/openlive/71cc61f54e394cc494d3a071f31948f3&amp;c=0&amp;p=1&amp;i=https://open.ys7.com/sdk/js/1.3/demo.html&amp;lv=1&amp;loaded=loadHandler" width="100%" height="100%" name="myPlayerflashId" id="myPlayerflashId" align="middle" type="application/x-shockwave-flash" pluginspage="http://www.macromedia.com/go/getflashplayer"></object> -->
                  <video src="../../../static/img/muddy.mp4" width="100%" height="100%" autoplay="autoplay" muted="muted" loop></video>
                </div>
                <div class="video-title">
                  <span class="video-name">{{ item.name }}</span>
                  <span class="video-state">
                    <i class="sl-icon" :class="item.state"></i>{{ item.stateText }}
                  </span>
                </div>
              </li>
           </ul>
         </div>
         <div class="video-control-panel">
           <el-scrollbar class="video-control-scroll">
             <h4 class="currency-mode-title">
               <i class="mode-title-icon"></i> 界面设置
               <span class="mode-title-line"></span>
             </h4>
             <h5 class="control-title"> 通道</h5>
             <div class="video-control-row">
               <button type="button"
                       :class="{active:videoNumber === 'one-video-list'}"
                       @click="setWindowNumber('one-video-list')"
                       class="control-window-btn one-window">
                 <i class="sl-icon"></i>
               </button>
               <button type="button"
                       :class="{active:videoNumber === 'four-video-list'}"
                       @click="setWindowNumber('four-video-list')"
                       class="control-window-btn four-window">
                 <i class="sl-icon"></i>
               </button>
               <button type="button"
                       :class="{active:videoNumber === 'nine-video-list'}"
                       @click="setWindowNumber('nine-video-list')"
                       class="control-window-btn nine-window">
                 <i class="sl-icon"></i>
               </button>
             </div>
             <h5 class="control-title">控制</h5>
             <div class="video-control-row video-control-fun-wrap">
               <button type="button" class="control-fun-btn cease-current-fun">
                 <i class="cease-current-icon"></i> 停止当前
               </button>
               <button type="button" class="control-fun-btn cease-all-fun">
                 <i class="cease-all-icon"></i> 停止全部
               </button>
               <button type="button" class="control-fun-btn snap-fun" @click= "capture">
                 <i class="snap-icon"></i> 本地抓拍
               </button>
               <button type="button" class="control-fun-btn videotape-fun">
                 <i class="videotape-icon"></i> 本地录像
               </button>
             </div>
             <h4 class="currency-mode-title">
               <i class="mode-title-icon"></i> 云台控制
               <span class="mode-title-line"></span>
             </h4>
             <div class="cloud-platform-control">
               <div class="clock-dial-fun">
                 <button title="向上" class="control-btn top-control"  @mousedown="ptzstart(0)" @mouseup="ptzstop(0)"><i></i></button>
                 <button title="向右上" class="control-btn top-right-control"  @mousedown="ptzstart(6)" @mouseup="ptzstop(6)"><i></i></button>
                 <button title="向右" class="control-btn right-control" @mousedown="ptzstart(3)" @mouseup="ptzstop(3)"><i></i></button>
                 <button title="向右下" class="control-btn right-bottom-control"  @mousedown="ptzstart(7)" @mouseup="ptzstop(7)"><i></i></button>
                 <button title="向下" class="control-btn bottom-control" @click="ptzstart(1)" @mouseup="ptzstop(1)"><i></i></button>
                 <button title="向左下" class="control-btn bottom-left-control" @mousedown="ptzstart(5)" @mouseup="ptzstop(5)"><i></i></button>
                 <button title="向左" class="control-btn left-control" @mousedown="ptzstart(2)" @mouseup="ptzstop(2)"><i></i></button>
                 <button title="向左上" class="control-btn left-top-control" @mousedown="ptzstart(4)" @mouseup="ptzstop(4)"><i></i></button>
                 <button title="初始化" class="center-control" @mouseup="ptzstop(0)"></button>
               </div>
               <div class="lens-control">
                 <div class="lens-control-item">
                   <i class="item-add-brn  el-icon-plus"  @mousedown="ptzstart(8)" @mouseup="ptzstop(8)"></i>
                   <span class="item-name">变倍</span>
                   <i class="item-reduce-brn el-icon-minus"  @mousedown="ptzstart(9)" @mouseup="ptzstop(9)"></i>
                 </div>
                 <div class="lens-control-item">
                   <i class="item-add-brn  el-icon-plus"></i>
                   <span class="item-name">光圈</span>
                   <i class="item-reduce-brn el-icon-minus"></i>
                 </div>
                 <div class="lens-control-item">
                   <i class="item-add-brn  el-icon-plus"  @mousedown="ptzstart(10)" @mouseup="ptzstop(10)"></i>
                   <span class="item-name">聚焦</span>
                   <i class="item-reduce-brn el-icon-minus" @mousedown="ptzstart(11)" @mouseup="ptzstop(11)"></i>
                 </div>
               </div>
             </div>
           </el-scrollbar>
         </div>
      </div>
    </div>
</template>

<script>
  // import waterRadioTree from '@/components/currency/waterRadioTree'
  let videoOne = [{
    name:"杨窖水闸#视频监控",
    state:'normal',
    stateText:'正常'
  }];
  let videoFour = [{
    name:"奉城站1#视频监控",
    state:'normal',
    stateText:'正常'
  },{
    name:"奉城站2#视频监控",
    state:'normal',
    stateText:'正常'
  },{
    name:"奉城站3#视频监控",
    state:'abnormal',
    stateText:'掉线'
  },{
    name:"奉城站4#视频监控",
    state:'normal',
    stateText:'正常'
  }];
  let videoNine = [{
    name:"奉城站1#视频监控",
    state:'normal',
    stateText:'正常'
  },{
    name:"奉城站2#视频监控",
    state:'normal',
    stateText:'正常'
  },{
    name:"奉城站3#视频监控",
    state:'normal',
    stateText:'正常'
  },{
    name:"奉城站4#视频监控",
    state:'normal',
    stateText:'正常'
  },{
    name:"奉城站5#视频监控",
    state:'abnormal',
    stateText:'掉线'
  },{
    name:"奉城站6#视频监控",
    state:'normal',
    stateText:'正常'
  },{
    name:"奉城站7#视频监控",
    state:'abnormal',
    stateText:'掉线'
  },{
    name:"奉城站8#视频监控",
    state:'normal',
    stateText:'正常'
  },{
    name:"奉城站9#视频监控",
    state:'abnormal',
    stateText:'掉线'
  }];
  export default {
      name: "realTimeVideo",
      data(){
        return{
          videoData:videoOne,
          videoNumber:'one-video-list',
          accessToken:''
        }
      },
      computed: {
        stationInfo: {
          get: function() {
            return this.$store.state.stationInfo;
          }
        }
      },
      methods:{
        setWindowNumber(cls){
          this.videoNumber = cls;
          if(cls === 'one-video-list'){
            this.videoData = videoOne;
          }else if(cls === 'four-video-list'){
            this.videoData = videoFour;
          }else if(cls === 'nine-video-list'){
            this.videoData = videoNine;
          }
        },
        capture(){
          this.$http.get('/waterenvapi/video/capture',{

            }).then((response)=>{
                let dataJson = response.data;
                if(dataJson.code === 200){
                  this.$message({
                    message: '截图成功！',
                    type: 'success'
                  });
                }
              }).catch((error)=>{

            });
        },
        getaccessToken(){
            this.$http.get('/waterenvapi/video/accessToken',{
            }).then((response)=>{
                let dataJson = response.data;
                if(dataJson.code === 200){
                 this.accessToken = dataJson.content.info;
                }
              }).catch((error)=>{

            });
          },
          ptzstart(direction){
            this.$http.get('/waterenvapi/video/ptzstart',{
              params:{
                accessToken: this.accessToken,
                direction:direction,
                speed:1
              }
            }).then((response)=>{

            }).catch((error)=>{

            });         
          },
          ptzstop(direction){
            this.$http.get('/waterenvapi/video/ptzstop',{
              params:{
                  accessToken: this.accessToken,
                  direction:direction,
                  speed:1
                }
              }).then((response)=>{

              }).catch((error)=>{

              });
          }
      },
      mounted(){
        this.getaccessToken();
      },
      components:{
        // waterRadioTree
      },
      watch: {
        stationInfo(value) {
          if (value.length > 0) {
            this.stationId = value[0].nodeId;
            this.stationName = value[0].nodeName;

          }
        }
      }
  }
</script>

<style scoped lang="less">
.real-time-video-page{
  height: 100%;
  .video-content-wrap{
    width: calc(100% - 282px);
    height: 100%;
    float: left;
    background: #616e7e;
    padding: 4px 0 0 4px;
    .video-content-list{
      height: 100%;
      li{
        .video-box{
          height: calc(100% - 36px);
          width: 100%;
          overflow: hidden;
          background: #1a1a1a;
                 video {
                    width: 100%;
                    height: 100%;
                    object-fit:fill;
                  }
        }
        .video-title{
          height: 36px;
          line-height: 36px;
          padding: 0 12px;
          color: #fff;
          background: url("../../assets/images/video/video-title-bg.png") repeat-x;
          .video-state{
            float: right;
            i{
              width: 12px;
              height: 12px;
              border-radius: 50%;
              margin-right: 7px;
            }
            .normal{
              background: #24c768;
            }
            .abnormal{
              background: #c72424;
            }
          }
        }
      }
    }
  }
  .one-video-list{
    li{
      height: calc(100% - 4px);
      width: calc(100% - 4px);
    }
  }
  .four-video-list{
    li{
      float: left;
      width: calc(50% - 4px);
      margin-right: 4px;
      height: calc(50% - 4px);
      margin-bottom: 4px;
    }
  }
  .nine-video-list{
    li{
      float: left;
      width: calc(33.3% - 4px);
      margin-right: 4px;
      height: calc(33.3% - 4px);
      margin-bottom: 4px;
    }
  }
  .video-control-panel{
    width: 282px;
    height: 100%;
    float: right;
    border: 1px solid #d9e6f7;
    .control-title{
      font-size: 14px;
      color: #166bce;
      padding-left: 12px;
      padding-top: 10px;
    }
    .control-title:before{
      content: '';
      display: inline-block;
      vertical-align: middle;
      margin-bottom: 3px;
      width: 8px;
      height: 8px;
      background: #166bce;
      margin-right: 6px;
    }
    .video-control-row{
      padding-top: 12px;
    }
    .control-window-btn{
      width: 40px;
      height: 40px;
      line-height: 38px;
      border: 1px solid #c6d1de;
      margin: 0 25px;
      text-align: center;
      i{
        width: 20px;
        height: 20px;
      }
    }
    .control-window-btn:hover,
    .control-window-btn.active{
      background: url('../../assets/images/gradualChange/default-btn-bg.png') repeat-y;
      background-size: 100% 2px;
    }
    .one-window i{
      background: url("../../assets/images/gradualChange/one-window.png") no-repeat center center;
    }
    .one-window:hover i,.one-window.active i{
      background: url("../../assets/images/gradualChange/one-window-active.png") no-repeat center center;
    }
    .four-window i{
      background: url("../../assets/images/gradualChange/four-window.png") no-repeat center center;
    }
    .four-window:hover i,.four-window.active i{
      background: url("../../assets/images/gradualChange/four-window-active.png") no-repeat center center;
    }
    .nine-window i{
      background: url("../../assets/images/gradualChange/nine-window.png") no-repeat center center;
    }
    .nine-window:hover i,.nine-window.active i{
      background: url("../../assets/images/gradualChange/nine-window-active.png") no-repeat center center;
    }
  }
  .video-control-scroll{
    height: 100%;
  }
  .video-control-fun-wrap{
    text-align: center;
    padding-bottom: 10px;
    button.control-fun-btn{
      border: 1px solid #c6d1de;
      height: 30px;
      line-height: 30px;
      width: 108px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
      i{
        display: inline-block;
        vertical-align: middle;
        width: 18px;
        height: 18px;
        margin-bottom: 3px;
      }
    }
    button.control-fun-btn:before,button.control-fun-btn:after{
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      transition: all 0.2s;
      z-index: -1;
    }
    button.control-fun-btn:before{
      background: #f2f6fa;
      top: 0;
      left: 0;
    }
    button.control-fun-btn:after{
      background: #e6edf4;
      top: 100%;
      left: 0;
    }
    button.control-fun-btn:hover:before{
      top: -100%;
    }
    button.control-fun-btn:hover:after{
      top: 0;
    }
    .cease-current-fun{
      margin-bottom: 8px;
      border-radius: 0 0 6px 0;
    }
    .cease-all-fun{
      margin-bottom: 8px;
      margin-left: 3px;
      border-radius: 0 0 0 6px;
    }
    .snap-fun{
      border-radius: 0 6px 0 0;
    }
    .videotape-fun{
      margin-left: 3px;
      border-radius: 6px 0 0 0;
    }
    .cease-current-icon{
      background: url("../../assets/images/index/cease-current-icon.png") no-repeat center center;
    }
    .cease-all-icon{
      background: url("../../assets/images/index/cease-all-icon.png") no-repeat center center;
    }
    .snap-icon{
      background: url("../../assets/images/index/snap-icon.png") no-repeat center center;
    }
    .videotape-icon{
      background: url("../../assets/images/index/videotape-icon.png") no-repeat center center;
    }
  }
  .cloud-platform-control{
    padding: 10px 0;
    .clock-dial-fun>.control-btn{
      width: 28px;
      height: 16px;
      background: url("../../assets/images/video/control-btn.png") no-repeat;
      position: absolute;
    }
    .clock-dial-fun>.control-btn:hover{
      background: url("../../assets/images/video/control-active-btn.png") no-repeat;
    }
    .clock-dial-fun{
      height: 226px;
      width: 226px;
      margin: 0 auto;
      background: url("../../assets/images/video/control-clock-dial.png") no-repeat;
      position: relative;
      .top-control{
        top: 22px;
        left: 99px;
      }
      .top-right-control{
        top: 42px;
        left: 160px;
        transform: rotate(45deg);
      }
      .right-control{
        top: 103px;
        left: 188px;
        transform: rotate(90deg);
      }
      .right-bottom-control{
        top: 164px;
        left: 163px;
        transform: rotate(135deg);
      }
      .bottom-control{
        top: 192px;
        left: 100px;
        transform: rotate(180deg);
      }
      .bottom-left-control{
        top: 162px;
        left: 40px;
        transform: rotate(-135deg);
      }
      .left-control{
        top: 105px;
        left: 16px;
        transform: rotate(-90deg);
      }
      .left-top-control{
        top: 45px;
        left: 44px;
        transform: rotate(-45deg);
      }
      .center-control{
        position: absolute;
        width: 38px;
        height: 36px;
        top: 50%;
        left: 50%;
        margin-top: -18px;
        margin-left: -19px;
        background: url("../../assets/images/video/initialization-icon.png") no-repeat center center;
      }
      .center-control:hover{
        background: url("../../assets/images/video/initialization-active-icon.png") no-repeat center center;
      }
    }
    .lens-control{
      text-align: center;
      padding-top: 20px;
      padding-bottom: 20px;
      user-select:none;
      .lens-control-item{
        display: inline-block;
        vertical-align: top;
        position: relative;
        width: 24px;
        line-height: 24px;
        height: 86px;
        text-align: center;
        border: 1px solid #c9d4e0;
        padding-top: 18px;
        margin: 0 30px;
        .item-add-brn,.item-reduce-brn{
          position: absolute;
          width: 24px;
          height: 24px;
          border: 1px solid #c9d4e0;
          border-radius: 50%;
          font-size: 14px;
          background: #fff;
          text-align: center;
          line-height: 24px;
          font-weight: 700;
          color: #cad5e1;
          cursor: pointer;
        }
        .item-add-brn{
          top: -10px;
          left: -1px;
        }
        .item-reduce-brn{
          bottom: -10px;
          left: -1px;
        }
        .item-add-brn:hover,.item-reduce-brn:hover{
          color: #166bce;
          border: 1px solid #166bce;
        }
      }
    }
  }
}
button{
  border: none;
  outline: none;
  cursor: pointer;
}
/* 小图标通用样式 */
.sl-icon{
  display: inline-block;
  vertical-align: middle;
  margin-bottom: 3px;
}
/* 模块标题通用图标 */
.mode-title-icon{
  display: inline-block;
  vertical-align: middle;
  margin-bottom: 3px;
  width: 16px;
  height: 16px;
  margin-right: 3px;
  background: url("../../assets/images/index/icon_Title_Prefix.png") no-repeat;
}
</style>



